.page {
  &__body {
    &--with-menu {
      overflow: hidden;
    }
  }
  background-color: #e5e5e5;
  &__dropdown-menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 4;
    transform: translateX(-100%);
    transition: transform 0.3s;
    &:target {
      transform: translateX(0);
    }
  }
}
